<template>
  <div class="ebook-wrap">
    <swipe v-show="show" class="ebook" ref="mySwipe" :auto="0" :defaultIndex="defaultPage" :continuous="false" @change="changeSwipe">

      <swipe-item :data-index="index/2" v-if="index % 2 === 0" v-for="(book, index) in bookList">
          <section :class="['left', 'page' + book['pageId']]" :data-page="book['pageId']" :data-original="fileServer+ DBJsonPath + book['pageImg']">
            <a :class="['spot_reads','s' + btn.eventtype]" href="javascript: void(0)" v-if="book['buttons'] && (btn.eventtype==5 || btn.eventtype==6 || btn.eventtype==7)" v-for="btn in (book.buttons && book.buttons.button)" @click="playAudio" :data-sort="btn['id']" :data-voicePath="fileServer+ DBJsonPath + btn['soundsrc']" :style="`width: ${(btn['width']*pxRate).toFixed(3)}px;height:${(btn['height']*pxRate).toFixed(3)}px;left: ${(btn['x']*pxRate).toFixed(3)}px;top: ${(btn['y']*pxRate).toFixed(3)}px;`"></a>

            <div :class="['icon_1', 'icon' + btn.eventtype]" href="javascript: void(0)" v-if="book['buttons'] && btn.eventtype==3" v-for="btn in (book.buttons && book.buttons.button)" @click="allRead" :data-sort="btn['id']"  :style="`left: ${(btn['x']*pxRate).toFixed(3)}px;top: ${(btn['y']*pxRate).toFixed(3)}px;`"></div>

            <div :class="['icon_1', 'icon' + btn.eventtype]" href="javascript: void(0)" v-if="book['buttons'] && btn.eventtype==8" v-for="btn in (book.buttons && book.buttons.button)" @click="wordRead" :data-sort="btn['id']"  :style="`left: ${(btn['x']*pxRate).toFixed(3)}px;top: ${(btn['y']*pxRate).toFixed(3)}px;`"></div>

          </section>
          <section :class="['right', 'page' + bookList[index+1]['pageId']]" v-if="bookList[index+1]" :data-page="bookList[index+1]['pageId']" :data-original="fileServer+ DBJsonPath + bookList[index+1]['pageImg']">
            <a :class="['spot_reads','s' + btn.eventtype]" href="javascript: void(0)" v-if="bookList[index+1]['buttons'] && (btn.eventtype==5 || btn.eventtype==6 || btn.eventtype==7)" v-for="btn in (bookList[index+1].buttons && bookList[index+1].buttons.button)" @click="playAudio" :data-sort="btn['id']" :data-voicePath="fileServer+ DBJsonPath + btn['soundsrc']" :style="`width: ${(btn['width']*pxRate).toFixed(3)}px;height:${(btn['height']*pxRate).toFixed(3)}px;left: ${(btn['x']*pxRate).toFixed(3)}px;top: ${(btn['y']*pxRate).toFixed(3)}px;`"></a>

            <div :class="['icon_1', 'icon' + btn.eventtype]" href="javascript: void(0)" v-if="bookList[index+1]['buttons'] && btn.eventtype==3" v-for="btn in (bookList[index+1].buttons && bookList[index+1].buttons.button)" @click="allRead" :data-sort="btn['id']"  :style="`left: ${(btn['x']*pxRate).toFixed(3)}px;top: ${(btn['y']*pxRate).toFixed(3)}px;`"></div>

            <div :class="['icon_1', 'icon' + btn.eventtype]" href="javascript: void(0)" v-if="bookList[index+1]['buttons'] && btn.eventtype==8" v-for="btn in (bookList[index+1].buttons && bookList[index+1].buttons.button)" @click="wordRead" :data-sort="btn['id']"  :style="`left: ${(btn['x']*pxRate).toFixed(3)}px;top: ${(btn['y']*pxRate).toFixed(3)}px;`"></div>
          </section>
      </swipe-item>

    </swipe>
    <audio id="audio">
      <source type="audio/mpeg">
    </audio>
  </div>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /*@import "../../codeBlocks/water.css";*/
  .ebook-wrap {
    width: 100%;
    height: 100%;
    display: flex;
    align-items:center; /*垂直居中*/
    justify-content: center;
  }
  .ebook {
    /*margin: 0 auto;*/
    padding: .2rem;
    box-shadow: 0 0 15px #ccc;
    box-sizing: border-box;
    color: #fff;
    text-align: center;
  }
  .ebook li section {
    float: left;
    color: #ff0000;
    font-size: 30px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
  }

  .ebook li section.left {
    /*background-color: rebeccapurple;*/
  }

  .ebook li section.right {
    /*background-color: olivedrab;*/
  }

  .ebook li section.left, .ebook li section.right {
    width: 50%;
    height: 100%;
  }

  /*点读样式*/
  .ebook .spot_reads {
    position: absolute;
    display: inline-block;
    box-sizing: border-box;
    z-index: 10;
  }

  .ebook .spot_reads.active {
    border: 2px solid #ff0000;
    border-radius: 10px;
  }
  /*水滴样式*/
  .ebook li section div.icon {
    background-size: 100%;
  }

  #audio {
    display: none;
  }
</style>

<script>
  import Vue from 'vue'
  import Swipe from './swipe.vue'
  import SwipeItem from './swipe-item.vue'
  import Config from '../../../config/serverConfig'
  import optLog from 'src/codeBlocks/optLog'

  export default {
    name: 'ebook',
    data() {
      return {
        fileServer: '',
        bookList: [],
        DBJsonPath: '',
        pxRate: 0,
        show: false,
        defaultPage: 0,
        audioStatus: 'paused',
        audio: null,
        //全文跟读
        aList:null,
        timeS:null,
        pageN:null,
        A:null,
        B:null,
        N:0
        //报听写

      }
    },
    components: {
      Swipe,
      SwipeItem
    },
    mounted () {
      Vue.onlineEnv?(this.fileServer = Config.fileServer):(this.fileServer = '')
      this.audio = this.$el.querySelector('#audio')
      this.initBookWrap()
      window.addEventListener('resize', ()=>{
        this.initBookWrap()
      })
    },
    methods: {
      // 页码跳转
      goto(index) {
        let dom = this.$el.querySelector(`section.page${index}`)
        if(!dom) {return}
        let idx = Number.parseInt(dom.parentNode.getAttribute('data-index'))
        this.$refs.mySwipe.goto(idx);
      },

      // 上一页
      prev () {
        this.$refs.mySwipe.prev();
      },

      // 下一页
      next () {
        this.$refs.mySwipe.next();
      },

      // 页码变换
      changeSwipe(newIndex, oldIndex, towards) {
        let n = Number.parseInt(this.$el.querySelectorAll('li')[newIndex].querySelector('section.left').getAttribute('data-page'))
        let oDom = this.$el.querySelectorAll('li')[oldIndex]
        let o = Number.parseInt(oDom.querySelector('section.left').getAttribute('data-page'))

        /*切换电子书关闭点读的声音*/
        let audio = this.audio
        if(this.audioStatus === 'playing') {
          audio.pause()
          let aDom = oDom.querySelector('.active')
          aDom && aDom.classList.remove('active')
        }

        /*返回要懒加载的书页*/
        let lazyArr = []
        let tArr = [newIndex - 2, newIndex - 1, newIndex, newIndex + 1, newIndex + 2, newIndex + 3]

        tArr.forEach(item=>{
          if(this.needLazyLoad(item)) {
            lazyArr.push(item)
          }
        })

        this.$emit('change', n, o, lazyArr, towards)
      },

      // 初始化电子书页
      initData (bookList, DBJsonPath) {
        (typeof DBJsonPath === 'undefined')?(this.DBJsonPath = 'data/49a92e48-1f39-4382-9273-0a0d521162c6/332/ebook/'):(this.DBJsonPath = DBJsonPath)
        this.bookList = bookList
        this.show = true
      },

      // playAudio
      playAudio (e) {
        /*判断当前是否正在滑动*/
        if(!!document.body.getAttribute('drag')) {
          return
        }
        let dom = e.target
        let url = dom.getAttribute('data-voicePath')
        let audio = this.audio

        audio.addEventListener("playing", ()=>{
          this.audioStatus = "playing"
        })
        audio.addEventListener("pause", ()=>{
          this.audioStatus = "paused"
        })

        if(this.audioStatus === 'playing') {
          audio.pause()
          if(dom.classList.contains('active')) {
            dom.classList.remove('active')
            return
          }
          let aDom = this.$el.querySelector('.is-active').querySelector('.active')
          aDom && aDom.classList.remove('active')
        }
        dom.classList.add('active')
        audio.addEventListener('ended', ()=>{
          dom.classList.remove('active')
        })
        audio.querySelector('source').setAttribute('src', url)
        audio.load()
        audio.currentTime = 0
        audio.volume = 1
        audio.play()

        //获取音频时长
        let me = this
        audio.ontimeupdate = function () {
          me.timeS = audio.duration
        }

        // 用户操作记录
        optLog.userOptLog({
          operId: ['innerResOpt', 'child', 'spotRead'],
          data: '点读'
        })

      },

      //全文跟读
      allRead(e){
        let me = this
        me.pageN = e.target.parentNode.getAttribute('data-page')
        me.aList = this.$el.querySelector('.page'+this.pageN).querySelectorAll('.s5')
        /*Array.prototype.forEach.call(aList, function(el, i){

        })*/
        /*for(var i = 0 ;i<aList.length;i++){

        }*/

        me.N = 0
        clearInterval(me.A)
        clearTimeout(me.B)

        me.audio.pause()  //先暂停
        me.aList[this.N].click()
        me.playAct()
        // 用户操作记录
        optLog.userOptLog({
          operId: ['innerResOpt','child','allRead'],
          data: '全文跟读'
        })
      },

      //全文跟读监听
      playAct(){
        let me = this
        //每0.5秒执行，并命名计时器名称为A
        me.A = setInterval(()=>{
          me.perSecondListen()
        }, 500)
      },

      //监控audio结束
      perSecondListen(){
        let me = this
        let audio = me.audio
        let obj = me.aList
        if(audio.ended){
          clearInterval(me.A)
          //延迟跟读(上一句时长的120%)
          me.B = setTimeout(()=>{
            me.playAct()
            me.N ++
            if(me.N>=obj.length){
              clearInterval(me.A)
              me.N = 0
            }else{
              obj[me.N].click()
            }
          },me.timeS*1000*1.2)
        }
      },

      //报听写
      wordRead(e){
        let me = this
        me.pageN = e.target.parentNode.getAttribute('data-page')
        me.aList = this.$el.querySelector('.page'+this.pageN).querySelectorAll('.s7')
        let arrS =[]
        me.aList.forEach((item)=>{
          arrS.push(item.getAttribute('data-voicepath'))
        })
        let arrWord = []
        let arrTime = []
        for(let i = 0 ; i < me.aList.length;i++){
          if(i==0){
            arrWord.push('../img/start01.mp3')
            arrTime.push("3")
            arrWord.push(arrS[i])
            arrTime.push("3")
            arrWord.push(arrS[i])
            arrTime.push("5")
          }else if(i == me.aList.length-1){
            //console.log("我是最后一个!");
            arrWord.push(arrS[i])
            arrTime.push("3")
            arrWord.push(arrS[i])
            arrTime.push("0")      //最后一个不需要延长时间
          }else if(i%2 == 0){
            //偶数
            arrWord.push(arrS[i])
            arrTime.push("3")
            arrWord.push(arrS[i])
            arrTime.push("5")
          }else{
            //奇数
            arrWord.push(arrS[i])
            arrTime.push("3")
            arrWord.push(arrS[i])
            arrTime.push("5")
          }
        }
        console.log(arrWord,arrTime)
        // 用户操作记录
        optLog.userOptLog({
          operId: ['innerResOpt','child','listen_write'],
          data: '报听写'
        })

        console.log('报听写')
      },
      //报听写监听
      playAct1(){
        let me = this
        //每0.5秒执行，并命名计时器名称为A
        me.C = setInterval(()=>{
          me.nextListen()
        }, 500)
      },

      nextListen(){

      },

      // 检测当前页码是否需要懒加载
      needLazyLoad (num) {
        let dom = this.$el.querySelector(`section.page${num}`)
        if(!!dom) {
          return !dom.getAttribute('style')
        }else {
          return false
        }
      },

      // init bookWrap
      initBookWrap () {
        let bookRate = 1046 / 747; //教材内页图片比例，高/宽
        let w = document.body.offsetWidth
        let h = document.body.offsetHeight
        let bookH = h-20
        let bookW = Number.parseInt(bookH*bookRate)
        if(bookW > w) {
          bookW = w
          bookH = Number.parseInt(w/bookRate)
        }
        this.$el.querySelector('.ebook').setAttribute('style', `width: ${bookW}px;height: ${bookH}px;`)
        setTimeout(()=>{
          let bookHPadding = parseFloat(getComputedStyle(this.$el.querySelector('.ebook'))['padding'])*2
          this.pxRate = ((bookH-bookHPadding)/1046)
        })
      }
    }
  }
</script>



